import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { Hidden } from "@material-ui/core";

import image1 from "../assets/image-1.svg";
import Check from "../assets/star.svg";
import { getUserDetail } from "../../../api/user";
import { UIButton } from "../../../Components/exports";

const Success = ({ className }) => {
  const [name, setName] = useState(null);
  const preload = async () => {
    const res = await getUserDetail();
    setName(res.Name);
  };

  useEffect(() => {
    preload();
  }, []);

  return (
    <section className={className}>
      <div className="content">
        <Hidden smDown implementation="js">
          <div className="block-left">
            <div className="block-fixed">
              <h1>
                {"Sign up and unbox "}
                <br />
                {"a pandora of marketing tools."}
              </h1>
              <img src={image1} alt="image1" className="block-img" />
            </div>
          </div>
        </Hidden>
        <div className="block-right">
          <div className="success">
            <div className="badge-image">
              <img src={Check} alt="success badge" />
            </div>
            <div className="title">{`Congratulations ${name}`}</div>
            <div className="subtitle">
              <p>{"Your account has been created!"}</p>
            </div>
            <div className="actions">
              <UIButton
                component={Link}
                to="business-setup"
                className="my-5 ms-0"
              >
                {"Set up your business"}
              </UIButton>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Success;
