import { makeStyles } from "@material-ui/core";

export default makeStyles((theme) => ({
  "sign-up-process": {
    backgroundColor: "#f3f7f9",
    display: "flex",
    flexDirection: "column",
    height: "100%",
  },

  "sign-up": {
    backgroundColor: "#ffffff",
    borderTopLeftRadius: "24px",
    borderTopRightRadius: "24px",
    boxShadow: "0 -4px 5px 0 rgb(0 0 0 / 8%)",
    flexGrow: 1,

    "& .content": {
      height: "100%",

      "& .block-right": {
        display: "flex",
        flexDirection: "column",
        height: "100%",

        "& .success": {
          alignItems: "center",
          display: "flex",
          flexDirection: "column",
          gap: "10px",
          justifyContent: "center",
          textAlign: "center",

          "& .badge-image": {
            paddingTop: "10px",
          },

          "& .title": {
            color: "#24272C",
            fontSize: "24px",
            fontWeight: 500,
            lineHeight: "34px",
          },

          "& .subtitle": {
            color: "#566171",
            fontSize: "15px",
            fontWeight: 400,
            lineHeight: "22px",
          },
        },
      },
    },
  },

  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    "sign-up": {
      "& .content": {
        "& .block-right": {},
      },
    },
  },
  [theme.breakpoints.up("md")]: {
    "sign-up-process": {
      backgroundColor: "#ffffff",
    },

    "sign-up": {
      paddingTop: "66px",
      minHeight: "max-content",

      "& .content": {
        alignItems: "center",
        display: "flex",
        height: "100%",
        justifyContent: "center",
        margin: "0 auto",
        width: "90%",

        "& .block-left": {
          height: "100%",
          display: "flex",
          justifyContent: "center",
          position: "relative",
          rowGap: "20px",
          flex: "1 0 auto",

          "& .block-fixed": {
            alignItems: "center",
            display: "flex",
            flexDirection: "column",
            height: "100%",
            justifyContent: "flex-start",
            position: "fixed",
          },

          "& h1": {
            color: "#3B414A",
            textAlign: "center",
            fontSize: "32px",
            fontWeight: 900,
            lineHeight: 1,
            marginTop: "85px",
          },

          "& .block-img": {
            maxWidth: "100%",
            height: "auto",
          },
        },

        "& .block-right": {
          borderLeft: "1px solid rgba(0, 0, 0, 0.1)",
          borderRight: "1px solid rgba(0, 0, 0, 0.1)",
          boxShadow: "0px 5px 40px rgba(18, 176, 238, 0.1)",
          flex: "0 1 auto",
          padding: "70px 50px",
          minHeight: "100%",
          width: "420px",
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {
    "sign-up": {
      "& .content": {
        gap: "90px",
        padding: "0 90px",
        width: "80%",
      },
    },
  },
  "@media(min-width:1300px)": {
    "sign-up": {
      "& .content": {
        // gap: "180px",
        // padding: "0 180px",
        maxWidth: "1300px",
      },
    },
  },
}));
